import React, { useState, useEffect, useRef } from 'react';
import { Network, DataSet } from 'vis-network/standalone';

// 改善版グラフ探索アルゴリズム比較アプリ
const ImprovedGraphAlgorithmDemo = () => {
  // グラフケースの選択
  const [selectedCase, setSelectedCase] = useState('case1');
  
  // アルゴリズム選択（複数選択可能に変更）
  const [selectedAlgorithms, setSelectedAlgorithms] = useState({
    bfs: true,
    dfs: true,
    dijkstra: true
  });
  
  const [isRunning, setIsRunning] = useState(false);
  const [speed, setSpeed] = useState(800);
  const [startNode, setStartNode] = useState(1);
  const [targetNode, setTargetNode] = useState(7);
  const [log, setLog] = useState([]);
  const [algorithmData, setAlgorithmData] = useState({
    bfs: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#FFA500', visitOrder: [] },
    dfs: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#7E57C2', visitOrder: [] },
    dijkstra: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#009688', visitOrder: [] }
  });
  const [comparisonVisible, setComparisonVisible] = useState(false);
  const [networkReady, setNetworkReady] = useState(false);
  const [currentAlgorithm, setCurrentAlgorithm] = useState(null);
  
  const containerRef = useRef(null);
  const networkRef = useRef(null);
  const nodesDatasetRef = useRef(null);
  const edgesDatasetRef = useRef(null);
  const logContainerRef = useRef(null);

  // グラフケースのデータ
  const graphCases = {
    case1: {
      name: '基本グラフ',
      description: '標準的な接続を持つグラフ',
      nodes: [
        { id: 1, label: 'A', title: 'ノードA' },
        { id: 2, label: 'B', title: 'ノードB' },
        { id: 3, label: 'C', title: 'ノードC' },
        { id: 4, label: 'D', title: 'ノードD' },
        { id: 5, label: 'E', title: 'ノードE' },
        { id: 6, label: 'F', title: 'ノードF' },
        { id: 7, label: 'G', title: 'ノードG' }
      ],
      edges: [
        { id: 'e1-2', from: 1, to: 2, label: '3' },
        { id: 'e1-3', from: 1, to: 3, label: '1' },
        { id: 'e2-4', from: 2, to: 4, label: '2' },
        { id: 'e2-5', from: 2, to: 5, label: '5' },
        { id: 'e3-5', from: 3, to: 5, label: '2' },
        { id: 'e4-6', from: 4, to: 6, label: '1' },
        { id: 'e5-6', from: 5, to: 6, label: '4' },
        { id: 'e5-7', from: 5, to: 7, label: '3' },
        { id: 'e6-7', from: 6, to: 7, label: '2' }
      ],
      positions: {
        1: { x: -200, y: 0 },
        2: { x: -100, y: -75 },
        3: { x: -100, y: 75 },
        4: { x: 0, y: -125 },
        5: { x: 0, y: 0 },
        6: { x: 100, y: -75 },
        7: { x: 100, y: 75 }
      }
    },
    case2: {
      name: '複数経路グラフ',
      description: '目的地に至る複数の経路がある',
      nodes: [
        { id: 1, label: 'A', title: 'ノードA' },
        { id: 2, label: 'B', title: 'ノードB' },
        { id: 3, label: 'C', title: 'ノードC' },
        { id: 4, label: 'D', title: 'ノードD' },
        { id: 5, label: 'E', title: 'ノードE' },
        { id: 6, label: 'F', title: 'ノードF' },
        { id: 7, label: 'G', title: 'ノードG' }
      ],
      edges: [
        { id: 'e1-2', from: 1, to: 2, label: '2' },
        { id: 'e1-3', from: 1, to: 3, label: '4' },
        { id: 'e1-4', from: 1, to: 4, label: '3' },
        { id: 'e2-5', from: 2, to: 5, label: '1' },
        { id: 'e3-5', from: 3, to: 5, label: '5' },
        { id: 'e3-6', from: 3, to: 6, label: '1' },
        { id: 'e4-6', from: 4, to: 6, label: '2' },
        { id: 'e5-7', from: 5, to: 7, label: '3' },
        { id: 'e6-7', from: 6, to: 7, label: '2' },
        { id: 'e2-6', from: 2, to: 6, label: '6' }
      ],
      positions: {
        1: { x: -250, y: 0 },
        2: { x: -125, y: -100 },
        3: { x: -125, y: 0 },
        4: { x: -125, y: 100 },
        5: { x: 0, y: -75 },
        6: { x: 0, y: 75 },
        7: { x: 125, y: 0 }
      }
    },
    case3: {
      name: 'トラップグラフ',
      description: '近道に見えて実は遠回りになる経路がある',
      nodes: [
        { id: 1, label: 'A', title: 'ノードA' },
        { id: 2, label: 'B', title: 'ノードB' },
        { id: 3, label: 'C', title: 'ノードC' },
        { id: 4, label: 'D', title: 'ノードD' },
        { id: 5, label: 'E', title: 'ノードE' },
        { id: 6, label: 'F', title: 'ノードF' },
        { id: 7, label: 'G', title: 'ノードG' },
        { id: 8, label: 'H', title: 'ノードH' }
      ],
      edges: [
        { id: 'e1-2', from: 1, to: 2, label: '1' },
        { id: 'e1-3', from: 1, to: 3, label: '2' },
        { id: 'e2-4', from: 2, to: 4, label: '10' },
        { id: 'e3-5', from: 3, to: 5, label: '3' },
        { id: 'e4-7', from: 4, to: 7, label: '1' },
        { id: 'e4-8', from: 4, to: 8, label: '2' },
        { id: 'e5-6', from: 5, to: 6, label: '2' },
        { id: 'e6-7', from: 6, to: 7, label: '1' },
        { id: 'e7-8', from: 7, to: 8, label: '1' }
      ],
      positions: {
        1: { x: -250, y: 0 },
        2: { x: -150, y: -75 },
        3: { x: -150, y: 75 },
        4: { x: -50, y: -75 },
        5: { x: -50, y: 75 },
        6: { x: 50, y: 75 },
        7: { x: 50, y: -50 },
        8: { x: 150, y: 0 }
      }
    }
  };

  // 現在選択中のグラフケースを取得
  const getCurrentCase = () => {
    return graphCases[selectedCase];
  };

  // 現在のノードとエッジを取得
  const getCurrentNodes = () => {
    const currentCase = getCurrentCase();
    return currentCase.nodes.map(node => {
      // 開始ノードと目的地ノードの特別スタイリング
      let borderColor = '#7AA3E5';
      let borderWidth = 2;
      
      if (node.id === startNode) {
        borderColor = '#FFA500';
        borderWidth = 3;
      }
      if (node.id === targetNode) {
        borderColor = '#FF0000';
        borderWidth = 3;
      }
      
      return {
        ...node,
        color: {
          background: '#97C2FC',
          border: borderColor
        },
        borderWidth: borderWidth
      };
    });
  };

  const getCurrentEdges = () => {
    const currentCase = getCurrentCase();
    return currentCase.edges.map(edge => ({
      ...edge,
      color: {
        color: '#848484',
        highlight: '#848484'
      },
      width: 2
    }));
  };

  // ログのスクロールを最下部に保持
  useEffect(() => {
    const box = logContainerRef.current;
    if (!box) return;
    box.scrollTo({
      top: box.scrollHeight,
      behavior: 'smooth'
    });
  }, [log]);

  // ネットワークの初期化
  useEffect(() => {
    // クリーンアップ関数を準備
    const cleanup = () => {
      if (networkRef.current) {
        networkRef.current.destroy();
        networkRef.current = null;
      }
    };
    
    // すでにネットワークがあれば破棄
    cleanup();
    
    if (containerRef.current) {
      try {
        // DataSetの作成
        nodesDatasetRef.current = new DataSet(getCurrentNodes());
        edgesDatasetRef.current = new DataSet(getCurrentEdges());
        
        // vis-networkオプション
        const options = {
          nodes: {
            shape: 'circle',
            size: 30,
            font: {
              size: 14,
              color: '#333333',
              face: 'arial'
            },
            borderWidth: 2,
            shadow: true
          },
          edges: {
            width: 2,
            shadow: true,
            font: {
              size: 12,
              align: 'middle'
            },
            arrows: {
              to: { enabled: false }
            }
          },
          physics: {
            enabled: false
          },
          layout: {
            randomSeed: 42,
            improvedLayout: true
          }
        };

        // ネットワークの作成
        networkRef.current = new Network(
          containerRef.current,
          { 
            nodes: nodesDatasetRef.current, 
            edges: edgesDatasetRef.current 
          },
          options
        );

        // ノードの位置を固定（見やすいレイアウト）
        const positions = getCurrentCase().positions;

        networkRef.current.setOptions({
          physics: false
        });

        getCurrentNodes().forEach(node => {
          networkRef.current.moveNode(node.id, positions[node.id].x, positions[node.id].y);
        });
        
        setNetworkReady(true);
      } catch (error) {
        console.error("グラフの初期化エラー:", error);
        setNetworkReady(false);
      }
    }
    
    return cleanup;
  }, [selectedCase, startNode, targetNode]);

  // ログにメッセージを追加
  const addLog = (message, algorithm = null) => {
    const prefix = algorithm ? `[${getAlgorithmName(algorithm)}] ` : '';
    setLog(prev => [...prev, prefix + message]);
  };

  // アルゴリズム名を取得
  const getAlgorithmName = (algorithm) => {
    const names = {
      bfs: '幅優先探索(BFS)',
      dfs: '深さ優先探索(DFS)',
      dijkstra: 'ダイクストラ法'
    };
    return names[algorithm] || algorithm;
  };

  // アルゴリズムのデータを更新
  const updateAlgorithmData = (algorithm, data) => {
    setAlgorithmData(prev => ({
      ...prev,
      [algorithm]: {
        ...prev[algorithm],
        ...data
      }
    }));
  };

  // 全ての状態をリセット
  const resetStates = () => {
    if (!nodesDatasetRef.current || !edgesDatasetRef.current) return;
    
    try {
      // ノードのリセット
      const updatedNodes = getCurrentNodes();
      const updatedEdges = getCurrentEdges();
      
      // 一括更新
      nodesDatasetRef.current.update(updatedNodes);
      edgesDatasetRef.current.update(updatedEdges);
      
      // その他の状態をリセット
      setAlgorithmData({
        bfs: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#FFA500', visitOrder: [] },
        dfs: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#7E57C2', visitOrder: [] },
        dijkstra: { steps: 0, pathLength: 0, distance: 0, path: [], color: '#009688', visitOrder: [] }
      });
      setLog([]);
      setComparisonVisible(false);
    } catch (error) {
      console.error("リセットエラー:", error);
    }
  };

  // ノードの更新
  const updateNode = (nodeId, options) => {
    if (!nodesDatasetRef.current) return;
    
    try {
      nodesDatasetRef.current.update({
        id: nodeId,
        ...options
      });
    } catch (error) {
      console.error(`ノード更新エラー (ID: ${nodeId}):`, error);
    }
  };

  // エッジの更新
  const updateEdge = (edgeId, options) => {
    if (!edgesDatasetRef.current) return;
    
    try {
      edgesDatasetRef.current.update({
        id: edgeId,
        ...options
      });
    } catch (error) {
      console.error(`エッジ更新エラー (ID: ${edgeId}):`, error);
    }
  };

  // エッジのIDを取得
  const getEdgeId = (fromNode, toNode) => {
    const currentCase = getCurrentCase();
    return currentCase.edges.find(edge => 
      (edge.from === fromNode && edge.to === toNode) ||
      (edge.from === toNode && edge.to === fromNode)
    )?.id;
  };

  // エッジの重みを取得
  const getEdgeWeight = (fromNode, toNode) => {
    const currentCase = getCurrentCase();
    const edge = currentCase.edges.find(edge => 
      (edge.from === fromNode && edge.to === toNode) ||
      (edge.from === toNode && edge.to === fromNode)
    );
    return edge ? Number(edge.label) : Infinity;
  };

  // 隣接ノードを取得
  const getNeighbors = (nodeId) => {
    const currentCase = getCurrentCase();
    return currentCase.edges
      .filter(edge => edge.from === nodeId || edge.to === nodeId)
      .map(edge => edge.from === nodeId ? edge.to : edge.from);
  };

  // 待機する
  let waitCounter = 0;
  const MAX_WAITS = 1000;
  const wait = async (ms = null) => {
    if (waitCounter > MAX_WAITS) {
      console.warn('最大待機回数を超えました。安全のため中断します。');
      return Promise.resolve();
    }
    waitCounter++;
    return new Promise(resolve => setTimeout(resolve, ms || speed));
  };

  // BFSアルゴリズムの実行
  const runBFS = async () => {
    const algorithm = 'bfs';
    setCurrentAlgorithm(algorithm);
    
    addLog(`幅優先探索（BFS）を開始します。開始ノード: ${getCurrentNodes().find(n => n.id === startNode)?.label}、目的地ノード: ${getCurrentNodes().find(n => n.id === targetNode)?.label}`, algorithm);
    await wait(300);
    
    const queue = [startNode];
    const visited = new Set();
    const previous = { [startNode]: null };
    const visitOrder = [];
    let steps = 0;
    
    // 開始ノードをキューに追加
    updateNode(startNode, {
      color: { background: '#FFE0B2', border: '#FFA500' },
      borderWidth: 3
    });
    
    addLog(`開始ノード ${getCurrentNodes().find(n => n.id === startNode)?.label} をキューに追加しました。`, algorithm);
    await wait();
    
    while (queue.length > 0) {
      steps++;
      
      // キューから取り出し
      const currentNode = queue.shift();
      visitOrder.push(currentNode);
      
      updateNode(currentNode, {
        color: { background: '#FFA500', border: '#FF8C00' },
        borderWidth: 4,
        size: 35
      });
      
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} を処理します。現在のキュー: [${queue.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(', ')}]`, algorithm);
      await wait();
      
      if (visited.has(currentNode)) continue;
      
      // 訪問済みにする
      visited.add(currentNode);
      updateNode(currentNode, {
        color: { background: '#FFCC80', border: '#FF8C00' },
        size: 30,
        borderWidth: currentNode === startNode ? 3 : currentNode === targetNode ? 3 : 2
      });
      
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} を訪問済みにしました。`, algorithm);
      await wait();
      
      // 目的地に到達した場合
      if (currentNode === targetNode) {
        addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} に到達しました！`, algorithm);
        break;
      }
      
      // 隣接ノードの処理
      const neighbors = getNeighbors(currentNode);
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} の隣接ノード: [${neighbors.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(', ')}]`, algorithm);
      
      for (const neighbor of neighbors) {
        if (!visited.has(neighbor) && !queue.includes(neighbor)) {
          queue.push(neighbor);
          previous[neighbor] = currentNode;
          
          // エッジを検討中として強調表示
          const edgeId = getEdgeId(currentNode, neighbor);
          updateEdge(edgeId, {
            color: { color: '#FFA500', highlight: '#FFA500' },
            width: 3
          });
          
          // 隣接ノードをキューに追加
          updateNode(neighbor, {
            color: { background: '#FFE0B2', border: '#FFA500' },
            borderWidth: neighbor === targetNode ? 3 : 2
          });
          
          addLog(`隣接ノード ${getCurrentNodes().find(n => n.id === neighbor)?.label} をキューに追加しました。`, algorithm);
          await wait();
        }
      }
    }
    
    // 経路がある場合、経路を表示
    if (previous[targetNode]) {
      const pathInfo = await visualizePath(previous, startNode, targetNode, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: pathInfo.pathLength,
        distance: pathInfo.distance,
        path: pathInfo.path,
        visitOrder
      });
    } else if (targetNode !== startNode) {
      addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} への経路が見つかりませんでした。`, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: 0,
        distance: Infinity,
        path: [],
        visitOrder
      });
    }
    
    setCurrentAlgorithm(null);
    return { algorithm, status: 'complete' };
  };

  // DFSアルゴリズムの実行
  const runDFS = async () => {
    const algorithm = 'dfs';
    setCurrentAlgorithm(algorithm);
    
    addLog(`深さ優先探索（DFS）を開始します。開始ノード: ${getCurrentNodes().find(n => n.id === startNode)?.label}、目的地ノード: ${getCurrentNodes().find(n => n.id === targetNode)?.label}`, algorithm);
    await wait(300);
    
    const stack = [startNode];
    const visited = new Set();
    const previous = { [startNode]: null };
    const visitOrder = [];
    let steps = 0;
    
    // 開始ノードをスタックに追加
    updateNode(startNode, {
      color: { background: '#D1C4E9', border: '#7E57C2' },
      borderWidth: 3
    });
    
    addLog(`開始ノード ${getCurrentNodes().find(n => n.id === startNode)?.label} をスタックに追加しました。`, algorithm);
    await wait();
    
    while (stack.length > 0) {
      steps++;
      
      // スタックから取り出し
      const currentNode = stack.pop();
      visitOrder.push(currentNode);
      
      updateNode(currentNode, {
        color: { background: '#7E57C2', border: '#5E35B1' },
        borderWidth: 4,
        size: 35
      });
      
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} を処理します。現在のスタック: [${stack.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(', ')}]`, algorithm);
      await wait();
      
      if (visited.has(currentNode)) continue;
      
      // 訪問済みにする
      visited.add(currentNode);
      updateNode(currentNode, {
        color: { background: '#B39DDB', border: '#7E57C2' },
        size: 30,
        borderWidth: currentNode === startNode ? 3 : currentNode === targetNode ? 3 : 2
      });
      
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} を訪問済みにしました。`, algorithm);
      await wait();
      
      // 目的地に到達した場合
      if (currentNode === targetNode) {
        addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} に到達しました！`, algorithm);
        break;
      }
      
      // 隣接ノード（反対の順序で処理）
      const neighbors = getNeighbors(currentNode).reverse();
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} の隣接ノード: [${neighbors.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(', ')}]`, algorithm);
      
      // DFSでの選択理由をより明確に
      addLog(`【DFSの特徴】スタックを使用し、最も新しく追加されたノードを次に探索します（後入れ先出し）`, algorithm);
      
      for (const neighbor of neighbors) {
        if (!visited.has(neighbor)) {
          stack.push(neighbor);
          previous[neighbor] = currentNode;
          
          // エッジを検討中として強調表示
          const edgeId = getEdgeId(currentNode, neighbor);
          updateEdge(edgeId, {
            color: { color: '#7E57C2', highlight: '#7E57C2' },
            width: 3
          });
          
          // 隣接ノードをスタックに追加
          updateNode(neighbor, {
            color: { background: '#D1C4E9', border: '#7E57C2' },
            borderWidth: neighbor === targetNode ? 3 : 2
          });
          
          addLog(`隣接ノード ${getCurrentNodes().find(n => n.id === neighbor)?.label} をスタックに追加しました。`, algorithm);
          await wait();
        } else {
          addLog(`隣接ノード ${getCurrentNodes().find(n => n.id === neighbor)?.label} はスキップします（既に訪問済み）`, algorithm);
          await wait(speed / 2);
        }
      }
    }
    
    // 経路がある場合、経路を表示
    if (previous[targetNode]) {
      const pathInfo = await visualizePath(previous, startNode, targetNode, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: pathInfo.pathLength,
        distance: pathInfo.distance,
        path: pathInfo.path,
        visitOrder
      });
    } else if (targetNode !== startNode) {
      addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} への経路が見つかりませんでした。`, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: 0,
        distance: Infinity,
        path: [],
        visitOrder
      });
    }
    
    setCurrentAlgorithm(null);
    return { algorithm, status: 'complete' };
  };

  // ダイクストラアルゴリズムの実行
  const runDijkstra = async () => {
    const algorithm = 'dijkstra';
    setCurrentAlgorithm(algorithm);
    
    addLog(`ダイクストラ法を開始します。開始ノード: ${getCurrentNodes().find(n => n.id === startNode)?.label}、目的地ノード: ${getCurrentNodes().find(n => n.id === targetNode)?.label}`, algorithm);
    await wait(300);
    
    // 距離と前のノードを追跡
    const distances = {};
    const previous = {};
    const unvisited = new Set();
    const visitOrder = [];
    let steps = 0;
    
    // 初期化
    getCurrentNodes().forEach(node => {
      distances[node.id] = node.id === startNode ? 0 : Infinity;
      previous[node.id] = null;
      unvisited.add(node.id);
    });
    
    addLog(`距離の初期化: 開始ノード ${getCurrentNodes().find(n => n.id === startNode)?.label} の距離 = 0、他のノードの距離 = ∞`, algorithm);
    await wait();
    
    while (unvisited.size > 0) {
      steps++;
      
      // 未訪問ノードの中で最も距離が短いノードを選択
      let currentNode = null;
      let shortestDistance = Infinity;
      
      for (const nodeId of unvisited) {
        if (distances[nodeId] < shortestDistance) {
          shortestDistance = distances[nodeId];
          currentNode = nodeId;
        }
      }
      
      // 全てのノードが訪問済みか、到達不可能なノードしか残っていない場合
      if (currentNode === null || distances[currentNode] === Infinity) {
        addLog(`残りのノードは到達不可能です。`, algorithm);
        break;
      }
      
      visitOrder.push(currentNode);
      
      // 現在のノードを強調表示
      updateNode(currentNode, {
        color: { background: '#4DB6AC', border: '#009688' },
        borderWidth: 4,
        size: 35
      });
      
      addLog(`現在処理中のノード: ${getCurrentNodes().find(n => n.id === currentNode)?.label}（距離: ${distances[currentNode]}）`, algorithm);
      await wait();
      
      // 目的地に到達した場合
      if (currentNode === targetNode) {
        addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} に到達しました！（最短距離: ${distances[targetNode]}）`, algorithm);
        break;
      }
      
      // 現在のノードを訪問済みにする
      unvisited.delete(currentNode);
      updateNode(currentNode, {
        color: { background: '#80CBC4', border: '#009688' },
        size: 30,
        borderWidth: currentNode === startNode ? 3 : currentNode === targetNode ? 3 : 2
      });
      
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} を訪問済みにしました。`, algorithm);
      await wait();
      
      // 隣接ノードの距離を更新
      const neighbors = getNeighbors(currentNode);
      addLog(`ノード ${getCurrentNodes().find(n => n.id === currentNode)?.label} の隣接ノード: [${neighbors.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(', ')}]`, algorithm);
      
      for (const neighbor of neighbors) {
        if (!unvisited.has(neighbor)) continue;
        
        // エッジの重みを取得
        const weight = getEdgeWeight(currentNode, neighbor);
        const edgeId = getEdgeId(currentNode, neighbor);
        
        // 検討中のエッジを強調表示
        updateEdge(edgeId, {
          color: { color: '#26A69A', highlight: '#26A69A' },
          width: 3
        });
        
        updateNode(neighbor, {
          color: { background: '#B2DFDB', border: '#26A69A' },
          borderWidth: neighbor === targetNode ? 3 : 2
        });
        
        addLog(`エッジ ${getCurrentNodes().find(n => n.id === currentNode)?.label} → ${getCurrentNodes().find(n => n.id === neighbor)?.label}（コスト: ${weight}）を検討中...`, algorithm);
        await wait();
        
        // 距離の更新
        const distanceThroughCurrent = distances[currentNode] + weight;
        
        if (distanceThroughCurrent < distances[neighbor]) {
          distances[neighbor] = distanceThroughCurrent;
          previous[neighbor] = currentNode;
          
          addLog(`ノード ${getCurrentNodes().find(n => n.id === neighbor)?.label} への距離を ${distanceThroughCurrent} に更新しました。`, algorithm);
          await wait();
        } else {
          addLog(`ノード ${getCurrentNodes().find(n => n.id === neighbor)?.label} への現在の距離 ${distances[neighbor]} が既に最適です。`, algorithm);
          await wait();
        }
      }
    }
    
    // 経路がある場合、経路を表示
    if (previous[targetNode]) {
      const pathInfo = await visualizePath(previous, startNode, targetNode, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: pathInfo.pathLength,
        distance: pathInfo.distance,
        path: pathInfo.path,
        visitOrder
      });
    } else if (targetNode !== startNode) {
      addLog(`目的地ノード ${getCurrentNodes().find(n => n.id === targetNode)?.label} への経路が見つかりませんでした。`, algorithm);
      updateAlgorithmData(algorithm, { 
        steps, 
        pathLength: 0,
        distance: Infinity,
        path: [],
        visitOrder
      });
    }
    
    setCurrentAlgorithm(null);
    return { algorithm, status: 'complete' };
  };

  // 経路の視覚化
  const visualizePath = async (previous, startNode, targetNode, algorithm) => {
    // 経路を構築
    let current = targetNode;
    const path = [current];
    
    while (previous[current] != null) {
      current = previous[current];
      path.unshift(current);
    }
    
    // 経路の距離を計算
    let totalDistance = 0;
    const edgeDetails = [];
    
    for (let i = 0; i < path.length - 1; i++) {
      const from = path[i];
      const to = path[i+1];
      const weight = getEdgeWeight(from, to);
      totalDistance += weight;
      edgeDetails.push(`${getCurrentNodes().find(n => n.id === from)?.label} → ${getCurrentNodes().find(n => n.id === to)?.label} (コスト: ${weight})`);
    }
    
    // 経路を視覚化
    addLog(`【最短経路】${path.map(id => getCurrentNodes().find(n => n.id === id)?.label).join(' → ')} (総コスト: ${totalDistance})`, algorithm);
    
    // 経路の各ステップを詳細に表示
    if (edgeDetails.length > 0) {
      addLog('【経路の詳細】', algorithm);
      edgeDetails.forEach((detail, index) => {
        addLog(`  ${index + 1}. ${detail}`, algorithm);
      });
    }
    
    // アルゴリズムに基づいた色を設定
    const colors = {
      bfs: { edge: '#E91E63', node: '#F8BBD0', border: '#E91E63' },
      dfs: { edge: '#9C27B0', node: '#E1BEE7', border: '#9C27B0' },
      dijkstra: { edge: '#00BCD4', node: '#B2EBF2', border: '#00BCD4' }
    };
    
    const color = colors[algorithm];
    
    // パスのエッジを強調表示
    for (let i = 0; i < path.length - 1; i++) {
      const from = path[i];
      const to = path[i+1];
      const edgeId = getEdgeId(from, to);
      
      updateEdge(edgeId, {
        color: { color: color.edge, highlight: color.edge },
        width: 4
      });
      
      await wait(speed / 2);
    }
    
    // パスのノードを強調表示
    for (const nodeId of path) {
      updateNode(nodeId, {
        color: { background: color.node, border: color.border },
        borderWidth: nodeId === startNode ? 3 : nodeId === targetNode ? 3 : 2
      });
      
      await wait(speed / 2);
    }
    
    return {
      pathLength: path.length,
      distance: totalDistance,
      path: path.map(id => getCurrentNodes().find(n => n.id === id)?.label)
    };
  };

  // 複数アルゴリズムを連続実行
  const runAllSelectedAlgorithms = async () => {
    if (isRunning || !networkReady) return;
    setIsRunning(true);
    resetStates();
    
    // 待機カウンターをリセット
    waitCounter = 0;
    
    try {
      const algorithms = [];
      if (selectedAlgorithms.bfs) algorithms.push(runBFS);
      if (selectedAlgorithms.dfs) algorithms.push(runDFS);
      if (selectedAlgorithms.dijkstra) algorithms.push(runDijkstra);
      
      // 各アルゴリズムを順番に実行
      for (const runAlgorithm of algorithms) {
        // グラフをリセット（前のアルゴリズムの視覚化を残す）
        if (runAlgorithm !== algorithms[0]) {
          // 一時的にノードとエッジをリセット
          const updatedNodes = getCurrentNodes();
          const updatedEdges = getCurrentEdges();
          
          nodesDatasetRef.current.update(updatedNodes);
          edgesDatasetRef.current.update(updatedEdges);
          
          await wait(1000);
        }
        
        await runAlgorithm();
      }
      
      // 比較結果を表示
      setComparisonVisible(true);
    } catch (error) {
      console.error("アルゴリズム実行エラー:", error);
      addLog(`エラーが発生しました: ${error.message}`);
    } finally {
      setIsRunning(false);
    }
  };

  // アルゴリズム選択の切り替え
  const toggleAlgorithmSelection = (algorithm) => {
    if (isRunning) return;
    
    setSelectedAlgorithms(prev => ({
      ...prev,
      [algorithm]: !prev[algorithm]
    }));
  };

  // ケースを変更
  const changeCase = (caseId) => {
    if (isRunning) return;
    
    setSelectedCase(caseId);
    resetStates();
    
    // ケースに応じて適切な開始・終了ノードを設定
    const newCase = graphCases[caseId];
    const nodeIds = newCase.nodes.map(n => n.id);
    const minNodeId = Math.min(...nodeIds);
    const maxNodeId = Math.max(...nodeIds);
    
    setStartNode(minNodeId);
    setTargetNode(maxNodeId);
  };

  // アルゴリズムの比較結果を表示するコンポーネント
  const ComparisonResults = () => {
    const selectedAlgorithmKeys = Object.keys(selectedAlgorithms).filter(key => selectedAlgorithms[key]);
    
    if (!comparisonVisible || selectedAlgorithmKeys.length === 0) {
      return null;
    }
    
    // 最短距離のアルゴリズムを判定
    const shortestDistanceAlgorithm = selectedAlgorithmKeys.reduce((shortest, current) => {
      return algorithmData[current].distance < algorithmData[shortest].distance ? current : shortest;
    }, selectedAlgorithmKeys[0]);
    
    // 最少ステップ数のアルゴリズムを判定
    const leastStepsAlgorithm = selectedAlgorithmKeys.reduce((least, current) => {
      return algorithmData[current].steps < algorithmData[least].steps ? current : least;
    }, selectedAlgorithmKeys[0]);
    
    return (
      <div className="bg-white rounded-lg shadow-md p-4 mb-4">
        <h2 className="text-xl font-semibold mb-3">アルゴリズム比較結果</h2>
        
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">アルゴリズム</th>
                <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">ステップ数</th>
                <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">経路長</th>
                <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">総コスト</th>
                <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">経路</th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {selectedAlgorithmKeys.map(algorithm => (
                <tr key={algorithm} className={`${
                  algorithm === shortestDistanceAlgorithm && algorithm === leastStepsAlgorithm ? 'bg-green-50' :
                  algorithm === shortestDistanceAlgorithm ? 'bg-blue-50' :
                  algorithm === leastStepsAlgorithm ? 'bg-yellow-50' : ''
                }`}>
                  <td className="px-3 py-2 whitespace-nowrap">
                    <div className="flex items-center">
                      <div className="w-3 h-3 rounded-full mr-2" style={{ backgroundColor: algorithmData[algorithm].color }}></div>
                      <div className="font-medium">{getAlgorithmName(algorithm)}</div>
                      {algorithm === shortestDistanceAlgorithm && algorithm === leastStepsAlgorithm && (
                        <span className="ml-2 px-2 py-0.5 text-xs bg-green-100 text-green-800 rounded-full">最適解</span>
                      )}
                      {algorithm === shortestDistanceAlgorithm && algorithm !== leastStepsAlgorithm && (
                        <span className="ml-2 px-2 py-0.5 text-xs bg-blue-100 text-blue-800 rounded-full">最短距離</span>
                      )}
                      {algorithm === leastStepsAlgorithm && algorithm !== shortestDistanceAlgorithm && (
                        <span className="ml-2 px-2 py-0.5 text-xs bg-yellow-100 text-yellow-800 rounded-full">最少ステップ</span>
                      )}
                    </div>
                  </td>
                  <td className="px-3 py-2 whitespace-nowrap">
                    {algorithmData[algorithm].steps}
                  </td>
                  <td className="px-3 py-2 whitespace-nowrap">
                    {algorithmData[algorithm].pathLength} ノード
                  </td>
                  <td className="px-3 py-2 whitespace-nowrap">
                    {algorithmData[algorithm].distance === Infinity ? '∞' : algorithmData[algorithm].distance}
                  </td>
                  <td className="px-3 py-2">
                    {algorithmData[algorithm].path.length > 0 
                      ? algorithmData[algorithm].path.join(' → ') 
                      : '経路なし'}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        
        <div className="mt-4">
          <h3 className="text-md font-medium mb-2">このケースでの分析</h3>
          <div className="text-sm text-gray-700 p-3 bg-gray-50 rounded">
            {selectedAlgorithmKeys.length > 1 ? (
              <>
                <p className="mb-2">
                  <strong>比較結果：</strong> 
                  {shortestDistanceAlgorithm === leastStepsAlgorithm ? (
                    `${getAlgorithmName(shortestDistanceAlgorithm)}が最適な選択です！距離とステップ数の両方で最良の結果となりました。`
                  ) : (
                    `${getAlgorithmName(shortestDistanceAlgorithm)}が最短距離を見つけましたが、${getAlgorithmName(leastStepsAlgorithm)}はより少ないステップ数で実行されました。`
                  )}
                </p>
                <ul className="list-disc pl-5 space-y-1">
                  {selectedAlgorithms.bfs && (
                    <li>
                      <strong>幅優先探索(BFS)：</strong> 同じ距離（ノード数）のノードから順に探索するため、辺の重みが同じ場合は最短経路を見つけます。今回は重み付きグラフのため、必ずしも最適な経路を見つけるとは限りません。
                    </li>
                  )}
                  {selectedAlgorithms.dfs && (
                    <li>
                      <strong>深さ優先探索(DFS)：</strong> できるだけ深く探索するため、最初に見つけた経路が最適とは限りません。複雑なグラフでは効率が悪くなる場合があります。
                    </li>
                  )}
                  {selectedAlgorithms.dijkstra && (
                    <li>
                      <strong>ダイクストラ法：</strong> エッジの重みを考慮するため、重み付きグラフでは最短経路を保証します。ただし、すべてのノードを順番に評価するため、ステップ数が多くなる場合があります。
                    </li>
                  )}
                </ul>
              </>
            ) : (
              <p>複数のアルゴリズムを選択すると、より詳細な比較結果が表示されます。</p>
            )}
          </div>
        </div>
        
        <div className="mt-4">
          <h3 className="text-md font-medium mb-2">一般的な特徴</h3>
          <div className="text-sm text-gray-700 grid grid-cols-1 md:grid-cols-3 gap-3">
            <div className="p-3 bg-orange-50 rounded">
              <strong className="block mb-1">幅優先探索(BFS)</strong>
              <p>同じ深さのノードを先に探索します。辺の重みが均一なら最短経路を見つけられますが、重み付きグラフでは最適解を保証しません。迷路の最短経路などに適しています。</p>
            </div>
            <div className="p-3 bg-purple-50 rounded">
              <strong className="block mb-1">深さ優先探索(DFS)</strong>
              <p>一つの経路をできるだけ深く探索します。連結成分の検出や閉路検出に役立ちますが、最短経路を見つけることは保証されません。深いグラフでは効率が悪くなる場合があります。</p>
            </div>
            <div className="p-3 bg-teal-50 rounded">
              <strong className="block mb-1">ダイクストラ法</strong>
              <p>エッジの重みを考慮して最短経路を見つけます。正の重み付きグラフで最短経路を保証します。カーナビなどの最短経路探索に使われています。負の重みには対応していません。</p>
            </div>
          </div>
        </div>
        
        <div className="mt-4">
          <h3 className="text-md font-medium mb-2">お子さんへの説明方法</h3>
          <div className="text-sm text-gray-700 p-3 bg-yellow-50 rounded">
            <p className="mb-2">
              <strong>幅優先探索(BFS)：</strong> 
              「お友達をつぎつぎに呼ぶような探し方だよ。まず近くのお友達に聞いて、それから遠くのお友達に聞いていくんだ。」
            </p>
            <p className="mb-2">
              <strong>深さ優先探索(DFS)：</strong> 
              「一本の道をとことん最後まで歩いてみて、行き止まりだったら戻って別の道を試すような探し方だよ。迷路を解く時みたいだね。」
            </p>
            <p>
              <strong>ダイクストラ法：</strong> 
              「地図で一番近い道を選びながら進むような探し方だよ。歩く時間が短い道を選んで目的地まで行くんだ。カーナビみたいに一番早く着く道を教えてくれるよ。」
            </p>
          </div>
        </div>
      </div>
    );
  };

  // アルゴリズムの説明コンポーネント
  const AlgorithmExplanation = () => (
    <div className="mt-6 bg-white rounded-lg shadow-md p-4">
      <h2 className="text-xl font-semibold mb-3">アルゴリズムの説明</h2>
      
      <div className="mb-4">
        <h3 className="text-lg font-medium mb-2">幅優先探索 (BFS)</h3>
        <p className="text-sm text-gray-700">
          幅優先探索は、始点から近いノードから順に探索するアルゴリズムです。キュー（先入れ先出し）を使って実装され、同じ深さのノードを全て訪問してから次の深さに進みます。
          このアルゴリズムは、無向グラフにおいて「辺の数」が最小の経路（最短経路）を見つけることができます（全ての辺のコストが等しい場合）。
        </p>
      </div>
      
      <div className="mb-4">
        <h3 className="text-lg font-medium mb-2">深さ優先探索 (DFS)</h3>
        <p className="text-sm text-gray-700">
          深さ優先探索は、可能な限り深く探索してから戻るアルゴリズムです。スタック（後入れ先出し）または再帰を使って実装され、一つの経路を最後まで探索してから別の経路を試します。
          迷路の解法や連結成分の検出などに使用されますが、最短経路を見つけることは保証されません。
        </p>
      </div>
      
      <div>
        <h3 className="text-lg font-medium mb-2">ダイクストラ法</h3>
        <p className="text-sm text-gray-700">
          ダイクストラのアルゴリズムは、重み付きグラフにおける単一始点最短経路問題を解くアルゴリズムです。
          各ステップで未訪問ノードの中から最も距離が短いノードを選択し、その隣接ノードの距離を更新します。
          このアルゴリズムは、正のコストを持つグラフにおいて最短経路を見つけることが保証されています。
        </p>
      </div>
    </div>
  );

  return (
    <div className="mx-auto max-w-6xl p-4">
      <h1 className="text-3xl font-bold text-center mb-6">グラフ探索アルゴリズム比較デモ</h1>
      
      {/* ケース選択セクション */}
      <div className="bg-white rounded-lg shadow-md p-4 mb-4">
        <h2 className="text-xl font-semibold mb-3">グラフのケース選択</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
          {Object.keys(graphCases).map(caseId => (
            <button
              key={caseId}
              className={`p-3 rounded-md text-left ${selectedCase === caseId ? 'bg-blue-100 border-2 border-blue-500' : 'bg-gray-100 hover:bg-gray-200'}`}
              onClick={() => changeCase(caseId)}
              disabled={isRunning}
            >
              <div className="font-medium">{graphCases[caseId].name}</div>
              <div className="text-sm text-gray-600">{graphCases[caseId].description}</div>
            </button>
          ))}
        </div>
      </div>
      
      {/* 比較結果表示エリア */}
      <ComparisonResults />
      
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-4">
        {/* グラフ表示エリア */}
        <div className="lg:col-span-2">
          <div className="bg-white rounded-lg shadow-md p-4 mb-4">
            <div ref={containerRef} className="h-96 border border-gray-200 rounded"></div>
          </div>
          
          {/* アルゴリズムログ */}
          <div className="bg-white rounded-lg shadow-md p-4">
            <h2 className="text-xl font-semibold mb-2">実行ログ</h2>
            <div 
              ref={logContainerRef} 
              className="h-60 overflow-y-auto border border-gray-200 rounded p-2 bg-gray-50"
            >
              {log.map((message, index) => (
                <div key={index} className="mb-1 text-sm">
                  <span className="font-mono text-xs mr-2 text-gray-500">{index + 1}.</span>
                  {message}
                </div>
              ))}
            </div>
          </div>
        </div>
        
        {/* コントロールパネル */}
        <div className="bg-white rounded-lg shadow-md p-4">
          <h2 className="text-xl font-semibold mb-4">コントロールパネル</h2>
          
          {/* アルゴリズム選択 */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              比較するアルゴリズム:
            </label>
            <div className="space-y-2">
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="bfs"
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                  checked={selectedAlgorithms.bfs}
                  onChange={() => toggleAlgorithmSelection('bfs')}
                  disabled={isRunning}
                />
                <label htmlFor="bfs" className="ml-2 block text-sm text-gray-900">
                  幅優先探索 (BFS)
                </label>
              </div>
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="dfs"
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                  checked={selectedAlgorithms.dfs}
                  onChange={() => toggleAlgorithmSelection('dfs')}
                  disabled={isRunning}
                />
                <label htmlFor="dfs" className="ml-2 block text-sm text-gray-900">
                  深さ優先探索 (DFS)
                </label>
              </div>
              <div className="flex items-center">
                <input
                  type="checkbox"
                  id="dijkstra"
                  className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                  checked={selectedAlgorithms.dijkstra}
                  onChange={() => toggleAlgorithmSelection('dijkstra')}
                  disabled={isRunning}
                />
                <label htmlFor="dijkstra" className="ml-2 block text-sm text-gray-900">
                  ダイクストラ法
                </label>
              </div>
            </div>
          </div>
          
          {/* ノード選択 */}
          <div className="grid grid-cols-2 gap-4 mb-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                開始ノード:
              </label>
              <select
                className="block w-full px-3 py-2 border border-gray-300 rounded-md"
                value={startNode}
                onChange={(e) => {
                  if (!isRunning) {
                    setStartNode(Number(e.target.value));
                  }
                }}
                disabled={isRunning}
              >
                {getCurrentNodes().map(node => (
                  <option key={node.id} value={node.id}>
                    {node.label}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                目的地ノード:
              </label>
              <select
                className="block w-full px-3 py-2 border border-gray-300 rounded-md"
                value={targetNode}
                onChange={(e) => {
                  if (!isRunning) {
                    setTargetNode(Number(e.target.value));
                  }
                }}
                disabled={isRunning}
              >
                {getCurrentNodes().map(node => (
                  <option key={node.id} value={node.id}>
                    {node.label}
                  </option>
                ))}
              </select>
            </div>
          </div>
          
          {/* アニメーション速度 */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              アニメーション速度:
            </label>
            <div className="flex items-center">
              <span className="mr-2 text-xs">遅い</span>
              <input
                type="range"
                min="300"
                max="1500"
                step="100"
                value={speed}
                onChange={(e) => setSpeed(Number(e.target.value))}
                disabled={isRunning}
                className="flex-1"
              />
              <span className="ml-2 text-xs">速い</span>
            </div>
            <div className="text-xs text-center mt-1 text-gray-500">
              {Math.round(speed)}ms
            </div>
          </div>
          
          {/* 実行ボタン */}
          <div className="mb-3">
            <button
              className={`w-full px-4 py-3 rounded-md font-medium ${isRunning || !networkReady || Object.values(selectedAlgorithms).every(v => !v)
                ? 'bg-gray-300 text-gray-500 cursor-not-allowed' 
                : 'bg-indigo-600 text-white hover:bg-indigo-700'}`}
              onClick={runAllSelectedAlgorithms}
              disabled={isRunning || !networkReady || Object.values(selectedAlgorithms).every(v => !v)}
            >
              {isRunning 
                ? `実行中... ${currentAlgorithm ? getAlgorithmName(currentAlgorithm) : ''}` 
                : networkReady 
                  ? Object.values(selectedAlgorithms).some(v => v) 
                    ? '選択したアルゴリズムを実行' 
                    : 'アルゴリズムを選択してください'
                  : 'グラフ読み込み中...'
              }
            </button>
          </div>
          
          {/* リセットボタン */}
          <div className="mb-5">
            <button
              className="w-full px-4 py-2 rounded-md bg-gray-200 text-gray-700 hover:bg-gray-300"
              onClick={resetStates}
              disabled={isRunning || !networkReady}
            >
              リセット
            </button>
          </div>
          
          {/* 色の説明 */}
          <div>
            <h3 className="text-sm font-medium text-gray-700 mb-2">色の説明:</h3>
            <div className="grid grid-cols-2 gap-1 text-xs">
              <div className="flex items-center">
                <div className="w-3 h-3 bg-blue-300 mr-1 rounded-full"></div>
                <span>未訪問</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 border-2 border-orange-500 mr-1 rounded-full"></div>
                <span>開始ノード</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 border-2 border-red-500 mr-1 rounded-full"></div>
                <span>目的地ノード</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 bg-orange-300 mr-1 rounded-full"></div>
                <span>BFS訪問済み</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 bg-purple-300 mr-1 rounded-full"></div>
                <span>DFS訪問済み</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 bg-teal-300 mr-1 rounded-full"></div>
                <span>ダイクストラ訪問済み</span>
              </div>
              <div className="flex items-center">
                <div className="w-3 h-3 bg-pink-200 border-2 border-pink-600 mr-1 rounded-full"></div>
                <span>最短経路</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      {/* アルゴリズムの説明 */}
      <AlgorithmExplanation />
    </div>
  );
};

export default ImprovedGraphAlgorithmDemo;